import React from 'react'
import PropTypes from 'prop-types';
// mui
import { Stack } from '@mui/system'
import { Avatar, Typography, useTheme } from '@mui/material';

// project
import { fShortDate } from 'utils/formatTime';

const AvatarWithUser = ({ orientation = "horizontal", color, outlined, size, name, createdAt, avatar, sx = {}, ...others }) => {
  const theme = useTheme();
  const colorSX = color && !outlined && { bgcolor: `${color}.main`, color: theme.palette.text.main };
  const outlinedSX = outlined && {
    bgcolor: color ? `${color}.main` : 'primary.main',
    color: theme.palette.text.main,
    border: '1 solid',
    borderColor: color ? `${color}.main` : 'primary.main',
  }
  let sizeSX = {};
  switch (size) {
    case 'xs':
      sizeSX = {
        width: theme.spacing(2),
        height: theme.spacing(2)
      }
      break;
    case 'sm':
      sizeSX = {
        width: theme.spacing(2),
        height: theme.spacing(2)
      }
      break;
    case 'md':
      sizeSX = {
        width: theme.spacing(2),
        height: theme.spacing(2)
      }
      break;
    case 'lg':
      sizeSX = {
        width: theme.spacing(2),
        height: theme.spacing(2)
      }
      break;
    default:
      sizeSX = {}
  }
  return (
    <Stack direction={orientation === 'vertical' ? 'column' : 'row'} spacing={2} alignItems={`center`}>
      <Avatar
        alt={name}
        src={avatar}
        sx={{
          ...theme.typography.commonAvatar,
          ...theme.typography.mediumAvatar,
          transition: 'all .2s ease-in-out',

          ...colorSX,
          ...outlinedSX,
          ...sizeSX,
          ...sx
        }}

        {...others}
      />
      <Typography variant='subtitle1' >{name} </Typography>
      {orientation === 'vertical' ? undefined : <Typography variant='h5'> {`  •  `}</Typography>}
      <Typography variant='body2'>{fShortDate(createdAt)}</Typography>
    </Stack>
  )
}

AvatarWithUser.propTypes = {
  color: PropTypes.string,
  outline: PropTypes.bool,
  size: PropTypes.string,
  sx: PropTypes.object,
  name: PropTypes.string,
  avatar: PropTypes.string.isRequired,
  createdAt: PropTypes.string,
  orientation: PropTypes.string,
}

export default AvatarWithUser;